import React from "react";
import AppHeader from './AppHeader/';
import AppFooter from './AppFooter/';
import { Route, Switch, HashRouter} from 'react-router-dom';
import "../../node_modules/react-vis/dist/style.css";
import {connect} from 'react-redux';
import MenuAction from '../actions/MenuAction/';
import Home from '../components/NavList/Home/';
import Iconography from '../components/NavList/Iconography/';
import AppAvatar from '../components/NavList/Components/Avatar/';
import AppButtons from '../components/NavList/Components/Buttons/';
import AppHeatmap from '../components/NavList/Charts/Heatmap/';
import AppScatterplot from '../components/NavList/Charts/ScatterPlot/';
import Color from '../components/NavList/Colors/';



class App extends React.Component {
  handleNavOpen = () => {
    this.props.leftMenuOpen();
  };

  render() {
    return(
       <HashRouter >
      <div className='App'>
         <div>
         <AppHeader SideBarOpen={this.handleNavOpen} listOpenState={this.props.leftOpen}/>
         </div>

         <div className={this.props.leftOpen?'move-right':'default'}>
             <div className = 'content'>
             <Switch>
             <Route path='/' component={Home} key='/home' exact />
             <Route path='/icons' component={Iconography} key='/icons' />
             <Route path = "/Colors" component = {Color} key = "/colors"/>
             <Route path='/components/avatar' component={AppAvatar} key='/components/avatar' />
             <Route path='/components/button' component={AppButtons} key='/components/button' />
             <Route path='/charts/scatterplot' component={AppScatterplot} key='/charts/scatterplot' />
             <Route path='/charts/heatmap' component={AppHeatmap} key='/charts/heatmap' />
             </Switch>
             </div>
         </div>
         <div className='footer-position'>
           <AppFooter />
         </div>
      </div>
      </HashRouter>
    );
  }
}
const mapStateToProps = (state) => {
console.log(state)
  return {
    leftOpen: state.menuReducer.leftOpen,
    data: state.menuReducer.data
  }
}

const mapDispatchtoProps = (dispatch) => {
  return {
    leftMenuOpen: () => {dispatch(MenuAction.LeftMenuOpen())},
  }
}

export default connect(mapStateToProps,mapDispatchtoProps)(App)
