import React from 'react'
import { NavLink } from 'react-router-dom'
import Avatar from '@material-ui/core/Avatar';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuItem from '@material-ui/core/MenuItem';
import MenuIcon from '@material-ui/icons/Menu';
import Menu from '@material-ui/core/Menu';
import LaunchIcon from '@material-ui/icons/Launch';
import List from '@material-ui/core/List';
import HomeIcon from '../../assets/images/it.png';
import {connect} from 'react-redux';
import HeaderAction from '../../actions/HeaderAction/';
import AppNav from '../AppNav/';
import Collapse from '@material-ui/core/Collapse';



class AppHeader extends React.Component {
  handleClick = (event) => {
    this.props.showRightMenu(event);
  };

  handleClose = () => {
    this.props.closeRightMenu();
  };

  displayMenu(content)
  {
    let menu = [];
    for( let i in content)
    {
      menu.push(<a href={content[i].url} key={content[i].label} target='_blank' rel='noopener noreferrer'><MenuItem key={content[i].label}>{content[i].label}</MenuItem></a>);
    }
    return(
      <div>
        {menu}
      </div>
    )
  }

  render() {
    const { anchorEl } = this.props;
    const { rightContent } = this.props;
    let output = this.displayMenu(rightContent);

    return (
      <div >
        <AppBar className='top' color='inherit'>
          <Toolbar className='toolbar'>
            <div className='left'>
              <IconButton edge='start' color='inherit' aria-label='menu' onClick={()=>{this.props.SideBarOpen()}} >
                <MenuIcon />
              </IconButton>
              <div className='app-header-menu-item'>
                <NavLink to='/'>
                  <Avatar src={HomeIcon} />
                </NavLink>
              </div>
              <Typography variant='h6'>
                HBBHBB
              </Typography>
            </div>
            <div className='right'>
              <IconButton edge='end' color='inherit' aria-haspopup='true' onClick={this.handleClick}>
                <LaunchIcon />
              </IconButton>
              <Menu className='menu' anchorEl={anchorEl} keepMounted open={Boolean(anchorEl)} onClose={this.handleClose}>
                {output}
              </Menu>
            </div>
          </Toolbar>
        </AppBar>
        <div>
          <List>
            <Collapse in={this.props.listOpenState} timeout='auto' unmountOnExit>
              <div className='list'>
                <List component='div'>
                  <AppNav />
                </List>
              </div>
            </Collapse>
          </List>
        </div>
      </div>
    )
  }
}
const mapStateToProps = (state) =>{
  return{
    anchorEl: state.headerReducer.anchorEl,
    rightContent: state.headerReducer.rightContent
  }
}

const mapDispatchtoProps = (dispatch) =>{
  return{
    showRightMenu: (event) => {dispatch(HeaderAction.ShowRightMenu(event))},
    closeRightMenu: () => {dispatch(HeaderAction.CloseRightMenu())}
  }
}


export default connect(mapStateToProps,mapDispatchtoProps)(AppHeader)
